import {connect} from "react-redux";

import {SelectMap} from "./SelectMap";

import {push} from "react-router-redux";
import {NAV_TARGET_MODE_SELECT} from "./constants";

function mapDispatchToProps(dispatch) {
    return {
        selectMap: (map) => {
            dispatch(push(NAV_TARGET_MODE_SELECT + "?map=" + map));
        }
    };
}
export const SelectMapContainer = connect(() => { return {}}, mapDispatchToProps)(SelectMap);


