import {connect} from "react-redux";

import {Training} from "./Training";
import {trainingBack, trainingNext} from "./actions";


function mapDispatchToProps(dispatch, {history }) {
    return {
        previous: () => {
            dispatch(trainingBack());
        },
        back: () => {
            history.goBack()
        },
        next: () => {
            dispatch(trainingNext());
        }
    };
}


function mapStateToProps(state) {
    const trainingState = state.training;

    if(state.training.data) {
        const trainingsData = trainingState.data;

        const currentSlide = trainingsData[trainingState.currentDescription].slides[trainingState.currentSlide]

        return {currentSlide};
    }

    else {
        return {};
    }



}

export const TrainingContainer = connect(mapStateToProps, mapDispatchToProps)(Training);


