import {SET_MAP, SET_MODE, SET_SIDE} from "./actions";

export const selection = function(state = {}, action) {

    switch (action.type) {
        case SET_MAP:
            return {...state, map: action.map};
        case SET_MODE:
            return {...state, mode: action.mode};
        case SET_SIDE:
            return {...state, side: action.side};
        default:
            return state;
    }
}


export default selection;


