import jQuery from "jquery";

function doubleEach($html, selector1, selector2, callback) {
    var $aList = $html.filter(selector1).add($html.find(selector1));
    var $bList = $html.filter(selector2).add($html.find(selector2));
    //var $bList = $html.find(selector2);

    //filter(selector).add(this.find(selector))

    if($aList.length !== $bList.length) {
        throw Error(`Invalid html! Different length for the selectors: ${selector1} and ${selector2} `);
    }

    for (var i = 0; i < $aList.length; i++) {
        const $a = jQuery($aList.get(i));
        const $b = jQuery($bList.get(i));
        callback($a, $b);
    }

}

export const parseHtml = function(html) {
    const $html = jQuery(html);

    var side;
    var result = [];

    $html.find('.article > *').each((index, element) => {
        let $element = jQuery(element);

        if($element.prop("tagName") === "H1" ) {
            side = $element.text();
        } else {
                doubleEach($element, 'h2', '.sect1', ($h2, $sect1) => {
                    const area = $h2.text();

                    doubleEach($sect1, 'h3', '.sect2', ($h3, $sect2) => {
                        const title = $h3.text();

                        const slides = [];

                        $sect2.find('.sect3').each((index, element) => {
                            const content = jQuery(element).html();
                            slides.push(content);

                        });

                        result.push({
                            side,
                            area,
                            title,
                            slides
                        });
                    });

                });
        }
    });


    return result;
};

export default parseHtml;


