import React from "react";
export const SelectMap = ({selectMap}) => {

    return (
        <div className="menu">
            <h1>Select Map</h1>
            <button onClick={() => {selectMap("de_mirage")}}>de_mirage</button>
            <button onClick={() => {selectMap("de_dust2")}}>de_dust2</button>
            <button onClick={() => {selectMap("de_nuke")}}>de_nuke</button>
        </div>
    );
}

export default SelectMap;


