import {SET_TRAININGS_DATA, TRAINING_BACK, TRAINING_NEXT, TRAINING_NEXT_SLIDE} from "./actions";

export const training = function(state = {}, action) {

    switch (action.type) {
        case SET_TRAININGS_DATA:
            return {...state, data: action.data, currentDescription: 0, currentSlide: 0, currentTimer: 0};
        case TRAINING_NEXT:
            return {...state, currentDescription: ((state.currentDescription + 1) % state.data.length), currentSlide: 0, currentTimer: 0};
        case TRAINING_BACK:
            var descriptionIndex = (state.currentDescription - 1);
            if (descriptionIndex < 0) {
                descriptionIndex = state.data.length - 1;
            }
            return {...state, currentDescription: descriptionIndex, currentSlide: 0, currentTimer: 0};
        case TRAINING_NEXT_SLIDE:
            if(state.data) {
                var nextSlide = (state.currentSlide + 1) % state.data[state.currentDescription].slides.length;
                return { ...state, currentSlide: nextSlide}

            } else {
                return { ...state, currentSlide: 0};
            }
        default:
            return state;
    }
}


export default training;


