import {put, takeEvery} from "redux-saga/effects";

import {SELECT_MAP, setMap} from "./actions";

import {NAV_TARGET_MODE_SELECT} from "./constants";

import {push} from "react-router-redux";

function* selectMap(action) {
    const map = action.map;

    yield(put(setMap(map)));
    yield(put(push(NAV_TARGET_MODE_SELECT)));
}

//Trigger {fetchVehicleAsync} every time an action of type ACTION_TYPE_VEHICLE_LOAD is dispatched.
export function* selectMapTrigger() {
    yield takeEvery(SELECT_MAP, selectMap)
}



