import createHistory from "history/createBrowserHistory";
import React, {Component} from "react";
import {Route} from "react-router";
import {applyMiddleware, combineReducers, compose, createStore} from "redux";
import {Provider} from "react-redux";
import createSagaMiddleware from "redux-saga";
import {ConnectedRouter, routerMiddleware, routerReducer} from "react-router-redux";
import { rootSaga } from "./main/sagas";
import { selection } from './main/selectionReducer'
import { data } from './main/dataReducer'



import { NAV_TARGET_MODE_SELECT, NAV_TARGET_TRAINING} from './main/constants'

import {SelectMapContainer} from "./main/SelectMapContainer";
import {SelectModeContainer} from "./main/SelectModeContainer";
import {SelectSideContainer} from "./main/SelectSideContainer";


import "./App.css";
import {TrainingContainer} from "./main/TrainingContainer";
import {training} from "./main/trainingReducer";


const history = createHistory()

// Build the routerMiddleware for intercepting and dispatching navigation actions
const routerHistoryMiddleware = routerMiddleware(history);
const sagaMiddleware = createSagaMiddleware();

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;


//create the one and only redux store
const store = createStore(combineReducers({selection, data, training, routerReducer}),
    composeEnhancers(applyMiddleware(sagaMiddleware),
        applyMiddleware(routerHistoryMiddleware))
);


sagaMiddleware.run(rootSaga);


class App extends Component {
    render() {
        //

        return (
            <Provider store={store}>
                <ConnectedRouter history={history}>
                    <div className="App">
                        <Route exact path="/" component={SelectMapContainer}/>
                        <Route exact path={"/" + NAV_TARGET_MODE_SELECT} component={SelectModeContainer}/>
                        <Route exact path={ NAV_TARGET_TRAINING} component={TrainingContainer}/>

                        <Route exact path="/select-side" component={SelectSideContainer}/>
                    </div>
                </ConnectedRouter>
            </Provider>
        );
    }
}

export default App;



