//search(pin): "?map=de_mirage"


import {put, take, takeEvery} from "redux-saga/effects";
import {delay} from "redux-saga";

import {
    LOAD_DATA_FINISHED,
    loadData,
    setTrainingsData,
    TRAINING_TICK,
    trainingNextSlide,
    trainingTick
} from "./actions";

import {NAV_TARGET_TRAINING} from "./constants";


function getQueryVariable(search, variable) {

    if(search.charAt(0) === "?") {
        search = search.substring(1, search.length);
    }

    var vars = search.split('&');
    for (var i = 0; i < vars.length; i++) {
        var pair = vars[i].split('=');
        if (decodeURIComponent(pair[0]) === variable) {
            return decodeURIComponent(pair[1]);
        }
    }
    throw Error("Param not found");
}

function* startTraining(action) {
    const search = action.payload.search;
    const map = getQueryVariable(search, "map");

    yield(put(loadData(map)));

    const {data} = yield(take(LOAD_DATA_FINISHED));

    const sortedData = data.sort((a, b) => {
        if(a.area === b.area) {
            return 1;
        } else {
            return a.area > b.area ? 1 : -1;
        }
    });

    yield(put(setTrainingsData(sortedData)));
}

function* autoSlide() {
    yield delay(4000);

    yield put(trainingNextSlide())
    yield put(trainingTick())
}

//Trigger {fetchVehicleAsync} every time an action of type ACTION_TYPE_VEHICLE_LOAD is dispatched.
export function* trainingTrigger() {
    yield takeEvery((action) => {
        return action.type === "@@router/LOCATION_CHANGE" && action.payload.pathname === NAV_TARGET_TRAINING;
    }, startTraining);


    yield takeEvery(TRAINING_TICK, autoSlide);

    yield(put(trainingTick()));
}





