import {connect} from "react-redux";

import {SelectMode} from "./SelectMode";
import {MODE_TRAINING, NAV_TARGET_TRAINING} from "./constants";

import {push} from "react-router-redux";

function mapDispatchToProps(dispatch, ownProps) {
    return {
        selectMode: (mode) => {
            if(mode === MODE_TRAINING) {
                dispatch(push(NAV_TARGET_TRAINING + ownProps.location.search));
            } else {
                dispatch(push("/todo"));
            }

        }
    };
}
export const SelectModeContainer = connect(() => { return {} }, mapDispatchToProps)(SelectMode);




