import React from "react";
import {MODE_LEXICON, MODE_TRAINING} from "./constants";

export const SelectMode = ({selectMode}) => {

    return (
        <div class="menu">
            <h1>Select Mode</h1>
            <button onClick={() => {selectMode(MODE_TRAINING)}}>Training</button>
            <button onClick={() => {selectMode(MODE_LEXICON)}}>Lexikon</button>
        </div>
    );
}

export default SelectMode;


