//search(pin): "?map=de_mirage"

import {call, put, takeEvery} from "redux-saga/effects";

import {LOAD_DATA, loadDataFinished, setData } from "./actions";
import {parseHtml} from "./readAdocHtml";


const myFetch = (url) => {
    return fetch(url)
        .then(response => {
            return response.text();
        })
        .catch(error => {
            throw error;
        })
};

function* loadData(action) {
    const map = action.map;

    try {
        // Tell redux-saga to call fetch with the specified options
        const result = yield(call(myFetch, `./data/${map}.adoc.html`));

        //horrible hack - but hey its a temp project
        var replacedResult = result.replace(/assets/g, 'data/assets').replace(/images/g, 'data/images');


        const parsedResult = parseHtml(replacedResult);

        yield put(setData(parsedResult));
        yield put(loadDataFinished(parsedResult));
    }
    catch (err) {
        alert('Error retrieving data: ' + err);
    }
}

//Trigger {fetchVehicleAsync} every time an action of type ACTION_TYPE_VEHICLE_LOAD is dispatched.
export function* loadDataTrigger() {
    yield takeEvery(LOAD_DATA, loadData);
}


