import {connect} from "react-redux";

import {SelectSide} from "./SelectSide";
import {selectSide} from "./actions";

import {push} from "react-router-redux";

import {NAV_TARGET_MAP_SELECT} from "./constants";


function mapDispatchToProps(dispatch) {
    return {
        selectSide: (side) => {
            dispatch(selectSide(side));
            dispatch(push(NAV_TARGET_MAP_SELECT));
        }
    };
}
export const SelectSideContainer = connect(() => {}, mapDispatchToProps)(SelectSide);


