export const SELECT_MAP = 'csgohelper/main/SELECT_MAP';
export const SELECT_SIDE = 'csgohelper/main/SELECT_SIDE';
export const SELECT_MODE = 'csgohelper/main/SELECT_MODE';

export const SET_MAP = 'csgohelper/main/SET_MAP';
export const SET_SIDE = 'csgohelper/main/SET_SIDE';
export const SET_MODE = 'csgohelper/main/SET_MODE';
export const SET_DATA = 'csgohelper/main/SET_DATA';
export const LOAD_DATA = 'csgohelper/main/LOAD_DATA';
export const LOAD_DATA_FINISHED = 'csgohelper/main/LOAD_DATA_FINISHED';
export const SET_TRAININGS_DATA = 'csgohelper/main/SET_TRAININGS_DATA';

export const TRAINING_NEXT = 'csgohelper/main/TRAINING_NEXT';
export const TRAINING_BACK = 'csgohelper/main/TRAINING_BACK';
export const TRAINING_TICK = 'csgohelper/main/TRAINING_TICK';
export const TRAINING_NEXT_SLIDE = 'csgohelper/main/TRAINING_NEXT_SLIDE';





export function selectSide(side) {
    return {
        type: SELECT_SIDE,
        side
    }
}


export function selectMap(map) {
    return {
        type: SELECT_MAP,
        map
    }
}

export function setMap(map) {
    return {
        type: SET_MAP,
        map
    }
}


export function selectMode(mode) {
    return {
        type: SELECT_MODE,
        mode
    }
}


export function setMode(mode) {
    return {
        type: SET_MODE,
        mode
    }
}

export function setData(data) {
    return {
        type: SET_DATA,
        data
    }
}


export function loadData(map) {
    return {
        type: LOAD_DATA,
        map
    }
}

export function loadDataFinished(data) {
    return {
        type: LOAD_DATA_FINISHED,
        data
    }
}

export function setTrainingsData(data) {
    return {
        type: SET_TRAININGS_DATA,
        data
    }
}

export function trainingNext() {
    return {
        type: TRAINING_NEXT
    }
}

export function trainingBack() {
    return {
        type: TRAINING_BACK
    }
}

export function trainingTick() {
    return {
        type: TRAINING_TICK
    };
}

export function trainingNextSlide() {
    return {
        type: TRAINING_NEXT_SLIDE
    };
}


