import {selectMapTrigger} from "./selectMapSaga";
import {trainingTrigger} from "./trainingSaga";

import {all} from "redux-saga/effects";
import {loadDataTrigger} from "./loadDataSaga";

//returns the root saga that should be added to the middleware
export function* rootSaga() {
    yield all([selectMapTrigger(), trainingTrigger(), loadDataTrigger()]);
}

export default rootSaga;


