import React from "react";
import {SIDE_COUNTER_TERRORIST, SIDE_TERRORIST} from "./constants";

export const SelectSide = ({selectSide}) => {

    return (
        <div>
            <h1>Select side</h1>
            <button onClick={() => {selectSide(SIDE_TERRORIST)}}>Terrorist</button>
            <button onClick={() => {selectSide(SIDE_COUNTER_TERRORIST)}}>Counter Terrorist</button>
        </div>
    );
}

export default SelectSide;


