import React from "react";

export const Training = ({currentSlide, previous, back, next}) => {

    return (
        <div id="training">

            <div>
                <div id="training-panel">
                    <button onClick={() => {previous()}}>&lt;</button>
                    <button onClick={() => {back()}}>Back</button>
                    <button onClick={() => {next()}}>&gt;</button>
                </div>
            </div>

            <div onClick={() => {next()}} className="slide-content" dangerouslySetInnerHTML={{__html: currentSlide}} />

        </div>
    );
}

export default Training;


